}
.diagonal-gradient {
  background: linear-gradient(to top, black, #00143d);
}
.barra {
  background: #efefef;
  height: 80vh;
  border-radius: 10px;
}
.cardGrande {
  background-color: #f3f3fb;
  padding: 5%;
}
.total {
  background-color: #f3f3fb;
  border-radius: 10px;
}
.botones {
  background: transparent;
  border: 0px solid transparent;
  color: #7f85a2;
  min-width: 100px;
  cursor: pointer;
  height: 30px;
  border-radius: 4px;
}
.botones :hover {
  color: #000;
}
button:hover {
  background-color: #00143d;
  color: white;
}

button:active {
  background-color: #000a1f;
  color: white;
}
.App-link {
  color: #61dafb;
}
.Icon {
  background-color: #5f8a07;
  height: 40px;
  width: 40px;
  border-radius: 10px;
  color: #ffffff;
  border: 1px solid #0000002a;
  font-weight: 600;
  font-family: "M PLUS Rounded 1c", sans-serif;
}
.IconA {
  background-color: #ffae00;
  height: 40px;
  width: 40px;
  border-radius: 10px;
  color: #ffffff;
  border: 1px solid #0000002a;
  font-weight: 600;
  font-family: "M PLUS Rounded 1c", sans-serif;
}

.arrancar {
  background-color: #5f8a07;
  border-radius: 4px;
  border: 1px solid rgb(201, 255, 201);
  height: 40px;
  width: 90%;
  margin: 1%;
  font-weight: 500;
}
.arrancar:disabled {
  background-color: #c4c4c4;
  color: rgb(105, 105, 105);
}
.despresurizar {
  background-color: #15798a;
  border-radius: 4px;
  border: 1px solid rgb(202, 223, 255);
  height: 40px;
  width: 90%;
  margin: 1%;
  font-weight: 500;
}
.despresurizar:disabled {
  background-color: #c4c4c4;
  color: rgb(105, 105, 105);
}
.detener {
  background-color: #ff2a2aff;
  border-radius: 4px;
  border: 1px solid rgb(255, 223, 226);
  height: 40px;
  width: 90%;
  margin: 1%;
  font-weight: 500;
}
.detener:disabled {
  background-color: #c4c4c4;
  color: rgb(105, 105, 105);
}
.detener:hover:not([disabled]) {
  background-color: #8a0f06;
  color: white;
  cursor: pointer;
}
.arrancar:hover:not([disabled]) {
  background-color: rgb(22, 46, 22);
  color: white;
  cursor: pointer;
}
.despresurizar:hover:not([disabled]) {
  background-color: #0c4650;
  color: white;
  cursor: pointer;
}
.despresurizar:disabled {
  background-color: #c4c4c4;
  color: rgb(105, 105, 105);
}
.cardModulo {
  padding: 1%;
  font-size: 1rem;
}
.temperatura {
  background-color: white;
  font-size: medium;
  padding: 2%;

  color: #000;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

